import React, { ChangeEvent, useEffect, useState } from 'react';
import Slider from '@material-ui/core/Slider';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Pagination from '@material-ui/lab/Pagination';
import {Button, Input} from "reactstrap";

import { CatalogCard } from '../../component/catalog-card';
import { catalogFilterPenguin } from '../../core/utils/network/catalog';
import { Filter } from '../../core/models/filter';

import { NFT } from '../../core/models/nft';
import './catalog-page.scss'
import { useUser } from '../../core/context-provider/user/user-context';
import { arrNftCategories } from '../../core/utils/category';
import {Penguin} from "../../core/models/penguin";
import {Canvas} from "../../component/canvas";
import { backgrounds, clothes, eyes, glasses, hairs, hats, mouths, necks, random_objects, skins } from "../../core/data/basic-images";

import 'bootstrap/dist/css/bootstrap.min.css';
import {useOverlay} from "../../core/context-provider/loading-overlay/loading-overlay-context";

export const countOnDisplay: number = 12;

export interface CatalogProps {
    location: any
}

export const CatalogPage = (props: CatalogProps) => {

    const { setIsActivity, setOverlayText } = useOverlay();

    const [penguin, setPenguin] = useState<Penguin>({
        background: '',
        skin: '',
        eye: '',
        mouth: '',
        cloth: '',
        glass: '',
        hair: '',
        hat: '',
        neck: '',
        random_object: '',
    });
    const [filterData, setFilterData] = useState<Filter>({
        penguin: penguin,
        offset: 0,
        limit: countOnDisplay,
    });
    const path = 'basic_images';

    const [pageCount, setPageCount] = useState<number>(0);
    const [penguins, setPenguins] = useState<Penguin[]>([]);

    useEffect(() => {
        setIsActivity(true);
        setOverlayText('...Loading');
        catalogFilterPenguin(filterData).then((result: [Penguin[], number]) => {
            setIsActivity(false);
            if(!result || !result[0]) {
                return;
            }
            setPenguins([...result[0]]);
            if(parseInt(String(result[1] / countOnDisplay)) === 0){
                setPageCount(parseInt(String(result[1] / countOnDisplay)));
            } else if(parseInt(String(result[1] / countOnDisplay)) > 0) {
                setPageCount(parseInt(String(result[1] / countOnDisplay)) + 1);
            }
        });
    }, []);

    return (
        <div className="d-flex flex-column flex-lg-row w-100 mt-200 border-1 border-secondary justify-content-between">
            <div className="d-flex flex-column col-12 col-lg-3 p-30">

                <div className="mt-10 flex-column">
                    <Button onClick={() => {
                        setFilterData({...filterData, penguin: penguin});
                        setIsActivity(true);
                        setOverlayText('...Filtering');
                        catalogFilterPenguin({...filterData, penguin: penguin}).then((result: [Penguin[], number]) => {
                            setIsActivity(false);
                            if(!result || !result[0]) {
                                return;
                            }
                            setPenguins([...result[0]]);
                            if(parseInt(String(result[1] / countOnDisplay)) === 0){
                                setPageCount(parseInt(String(result[1] / countOnDisplay)));
                            } else if(parseInt(String(result[1] / countOnDisplay)) > 0) {
                                setPageCount(parseInt(String(result[1] / countOnDisplay)) + 1);
                            }
                        });
                    }}>
                        Filter Penguin
                    </Button>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Background</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const background = e.target.value.length > 0 ? `${path}/backgrounds/${e.target.value}` : '';
                               setPenguin({ ...penguin, background: background });
                           }}
                    >
                        <>
                            <option label='' value='' />
                            {
                                backgrounds.map((background, index) => (
                                        <option key={index} label={background.replace('.png', '')} value={background} />
                                    )
                                )
                            }
                        </>
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Skin</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const skin = e.target.value.length > 0 ? `${path}/skins/${e.target.value}` : '';
                               setPenguin({ ...penguin, skin: skin });
                           }}
                    >
                        <>
                            <option label='' value='' />
                            {
                                skins.map((skin, index) => (
                                        <option key={index} label={skin.replace('.png', '')} value={skin} />
                                    )
                                )
                            }
                        </>
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Eye</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const eye = e.target.value.length > 0 ? `${path}/eyes/${e.target.value}` : '';
                               setPenguin({ ...penguin, eye: eye });
                           }}
                    >
                        <>
                            <option label='' value='' />
                            {
                                eyes.map((eye, index) => (
                                        <option key={index} label={eye.replace('.png', '')} value={eye} />
                                    )
                                )
                            }
                        </>
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Mouth</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const mouth = e.target.value.length > 0 ? `${path}/mouths/${e.target.value}` : '';
                               setPenguin({ ...penguin, mouth: mouth });
                           }}
                    >
                        {
                            <>
                                <option label='' value='' />
                                {
                                    mouths.map((mouth, index) => (
                                            <option key={index} label={mouth.replace('.png', '')} value={mouth} />
                                        )
                                    )
                                }
                            </>
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Cloth</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const cloth = e.target.value.length > 0 ? `${path}/cloths/${e.target.value}` : '';
                               setPenguin({ ...penguin, cloth: cloth });
                           }}
                    >
                        {
                            clothes.map((cloth, index) => (
                                    <option key={index} label={cloth.replace('.png', '')} value={cloth} />
                                )
                            )
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Glass</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const glass = e.target.value.length > 0 ? `${path}/glasses/${e.target.value}` : '';
                               setPenguin({ ...penguin, glass: glass });
                           }}
                    >
                        {
                            glasses.map((glass, index) => (
                                    <option key={index} label={glass.replace('.png', '')} value={glass} />
                                )
                            )
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Hair</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const hair = e.target.value.length > 0 ? `${path}/hairs/${e.target.value}` : '';
                               setPenguin({ ...penguin, hair: hair });
                           }}
                    >
                        {
                            hairs.map((hair, index) => (
                                    <option key={index} label={hair.replace('.png', '')} value={hair} />
                                )
                            )
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Hat</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const hat = e.target.value.length > 0 ? `${path}/hats/${e.target.value}` : '';
                               setPenguin({ ...penguin, hat: hat });
                           }}
                    >
                        {
                            hats.map((hat, index) => (
                                    <option key={index} label={hat.replace('.png', '')} value={hat} />
                                )
                            )
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Neck</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const neck = e.target.value.length > 0 ? `${path}/necks/${e.target.value}` : '';
                               setPenguin({ ...penguin, neck: neck });
                           }}
                    >
                        {
                            necks.map((neck, index) => (
                                    <option key={index} label={neck.replace('.png', '')} value={neck} />
                                )
                            )
                        }
                    </Input>
                </div>
                <div className="mt-10 flex-column">
                    <h4 className="text-white">Random_Objects</h4>
                    <Input className="mt-5" type="select" name="select" id="exampleSelect"
                           onChange={(e) => {
                               const random_object = e.target.value.length > 0 ? `${path}/random_objects/${e.target.value}` : '';
                               setPenguin({ ...penguin, random_object: random_object });
                           }}
                    >
                        {
                            random_objects.map((random_object, index) => (
                                    <option key={index} label={random_object.replace('.png', '')} value={random_object} />
                                )
                            )
                        }
                    </Input>
                </div>
            </div>

            <div className="d-flex flex-column justify-content-between col-12 col-lg-9 justify-content-center my-30">
                <div className="row min-vh-100">
                    {
                        penguins.map((item, index) => (
                                <CatalogCard penguin={item} key={index}/>
                            )
                        )
                    }
                </div>
                <div className="d-flex justify-content-center my-20">
                    {
                        pageCount > 1 && (
                            <Pagination count={ pageCount } variant="outlined" shape="rounded"
                                        onChange={(event, value) => {
                                            const offset = (value - 1) * countOnDisplay;
                                            setFilterData({...filterData, offset: offset});
                                            setOverlayText('...Loading');
                                            setIsActivity(true);
                                            catalogFilterPenguin({...filterData, offset: offset}).then(async (result: [Penguin[], number]) => {
                                                setOverlayText('...Loading');
                                                setIsActivity(false);
                                                if(!result) {
                                                    return;
                                                }
                                                setPenguins([...result[0]]);
                                            });
                                        }}
                            />
                        )
                    }
                </div>
            </div>
        </div>
    );
}
