import React, { useEffect, useState } from 'react';
import { Link, NavLink } from 'react-router-dom';

import { useUser } from '../../core/context-provider/user/user-context';

import { ROUTES } from '../../core/data/routes';
import { toast } from '../../core/utils/notification.util';
import { isEthNetwork, login } from '../../core/utils/network/user';
import { getLocalStorageWalletAddress, lengthOfAddress, setLocalStorageWalletStatus } from '../../core/utils/wallet';

import '../layout.scss';
import { RouteItem } from '../../core/models/route';
import { Search } from '../../core/models/search';
import { arrNftCategories } from '../../core/utils/category';

declare global {
    interface Window {
        ethereum: any;
    }
}

export const Header = () => {

    const arrCategory: string[] = ['All Category'].concat(arrNftCategories);
    const [category, setCategory] = useState('All Category');

    const { walletAddress, setWalletAddress, setToken, connectWallet } = useUser();
    const [searchData, setSearchData] = useState<Search>({
        keyWord: '',
        category: arrNftCategories,
    });

    useEffect(() => {
        loadWallet().then();
    },[]);

    const mainPages: RouteItem[] = [
        {
            name: 'CATALOG',
            route: ROUTES.catalog
        }, {
            name: 'CREATE',
            route: ROUTES.createNFT
        }
        // {
        //     name: 'HOME',
        //     route: ROUTES.home
        // }, {
        //     name: 'CATALOG',
        //     route: ROUTES.catalog
        // }, {
        //     name: 'NEWS',
        //     route: ROUTES.news
        // }, {
        //     name: 'YOUR PROFILE',
        //     route: ROUTES.profile
        // }, {
        //     name: 'FAVORITES',
        //     route: ROUTES.favorite
        // }, {
        //     name: 'SUBSCRIPTION',
        //     route: ROUTES.subscription
        // }, {
        //     name: 'CREATE',
        //     route: ROUTES.createNFT
        // }, {
        //     name: 'SELL',
        //     route: ROUTES.sell
        // }
    ];

    const disconnectWallet = () => {
        setWalletAddress('');
        setLocalStorageWalletStatus('').then();
    }

    const loadWallet = async () => {
        if (!await isEthNetwork()) {
            setWalletAddress('');
            return;
        }
        if(getLocalStorageWalletAddress() !== '') {
            setWalletAddress(getLocalStorageWalletAddress());
        }
    }

    return (
        <header className="header">
            <div className="header__wrap">
                <div className="d-flex flex-column justify-content-center height-70">
                    <div className="d-none d-lg-flex">
                        <div className="d-flex w-100 h-100 justify-content-between">
                            <div className="d-flex w-100 h-100 justify-content-around">
                                <div>
                                    {
                                        mainPages.map((page: RouteItem, index) => (
                                            <NavLink activeClassName="active" className="ml-50" to={ page.route } key={ index }>
                                                <span className="font-weight-bold">{ page.name }</span>
                                            </NavLink>
                                        ))
                                    }
                                </div>
                                <div className="ml-40">
                                    {
                                        walletAddress.length === lengthOfAddress ? (
                                            <button className="min-width-135 btn btn-danger"
                                                    onClick={ () => disconnectWallet() }>Disconnect Wallet</button>
                                        ) : (
                                            <button className="min-width-135 btn btn-danger"
                                                    onClick={ () => connectWallet() }>Connect Wallet</button>
                                        )
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    );

}
