import React, { MouseEventHandler, useEffect, useState } from 'react';
import { BigNumber } from 'bignumber.js';
import { Button } from 'reactstrap';
import Web3 from 'web3';
import { create } from 'ipfs-http-client';

import {Canvas} from "../../component/canvas";
import {Penguin} from "../../core/models/penguin";
import {getRandomInt} from "../../core/utils/random";
import { backgrounds, clothes, eyes, glasses, hairs, hats, mouths, necks, random_objects, skins } from "../../core/data/basic-images";
import {uploadPenguin} from "../../core/utils/network/penguin";
import {useOverlay} from "../../core/context-provider/loading-overlay/loading-overlay-context";
import {toast} from "../../core/utils/notification.util";

const Buffer = require('buffer/').Buffer;

export const CreateNftPage = () => {

    const ipfs = create({ host: 'ipfs.infura.io', port: 5001, protocol: 'http' });

    const [generatedPenguin, setGeneratedPenguin] = useState<Penguin>();

    const { setIsActivity, setOverlayText } = useOverlay();

    useEffect(() => {
        const tempPenguin: Penguin = createRandomPenguin();
        setGeneratedPenguin(tempPenguin);
    },[]);

    const createRandomPenguin = (): Penguin => {
        const background: string = backgrounds[getRandomInt(backgrounds.length)];
        const skin: string = skins[getRandomInt(skins.length)];
        const eye: string = eyes[getRandomInt(eyes.length)];
        const mouth: string = mouths[getRandomInt(mouths.length)];
        const cloth: string = clothes[getRandomInt(clothes.length)];
        const glass: string = glasses[getRandomInt(glasses.length)];
        const hair: string = hairs[getRandomInt(hairs.length)];
        const hat: string = hats[getRandomInt(hats.length)];
        const neck: string = necks[getRandomInt(necks.length)];
        const random_object: string = random_objects[getRandomInt(random_objects.length)];

        const path = 'basic_images'

        const penguin: Penguin = {
            background: `${path}/backgrounds/${background}`,
            skin: `${path}/skins/${skin}`,
            eye: `${path}/eyes/${eye}`,
            mouth: `${path}/mouths/${mouth}`,
            cloth: cloth.length === 0 ? '' : `${path}/clothes/${cloth}`,
            glass: glass.length === 0 ? '' : `${path}/glasses/${glass}`,
            hair: hair.length > 0 && hat.length === 0 ? `${path}/hairs/${hair}` : '',
            hat: hat.length === 0 ? '' : `${path}/hats/${hat}`,
            neck: neck.length === 0 ? '' : `${path}/necks/${neck}`,
            random_object: random_object.length === 0 ? '' : `${path}/random_objects/${random_object}`,
        }

        return penguin;
    }

    return (
        <div className="d-flex w-100 justify-content-center">
            <div className="d-flex flex-column mt-200 min-vh-100">
                <h1 className="text-white">Generate Penguin</h1>
                <div className="d-flex flex-column justify-content-between bg-dark border-1 border-radius-10 border-secondary mt-20 p-20">
                    <div className="d-flex justify-content-center equal width-400 height-400 align-items-center border-1 border-radius-10 border-secondary">
                        <Canvas penguin={generatedPenguin} className="w-100 h-100 border-radius-10"/>
                    </div>
                    <Button
                        className="mt-20"
                        color="primary"
                        onClick={
                            () => {
                                const tempPenguin: Penguin = createRandomPenguin();
                                setGeneratedPenguin(tempPenguin);
                            }}>
                        Generate
                    </Button>
                    <Button
                        className="mt-10"
                        color="success"
                        onClick={ () => {
                            uploadPenguin({ hashPath: '' , ...generatedPenguin})?.then((result) => {
                                setIsActivity(false);
                                toast('success', 'Upload Success.');
                                const tempPenguin: Penguin = createRandomPenguin();
                                setGeneratedPenguin(tempPenguin);
                            });
                            // const json = JSON.stringify(generatedPenguin);
                            // const buffer = Buffer.from(json);
                            // setIsActivity(true);
                            // setOverlayText('...Uploading NFTS');
                            // ipfs.add(buffer)
                            // .then((ipfsHash: any) => {
                            //     uploadPenguin({ hashPath: ipfsHash.path , ...generatedPenguin})?.then((result) => {
                            //         setIsActivity(false);
                            //         toast('success', 'Upload Success.');
                            //         const tempPenguin: Penguin = createRandomPenguin();
                            //         setGeneratedPenguin(tempPenguin);
                            //     });
                            // });
                        }}>
                        Upload
                    </Button>
                </div>
            </div>
        </div>
    );
};
